@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.game-container{position:relative;color:white;width:100%;height:100%;overflow:hidden;display:flex;background:black}.role-container{width:100%;height:100%;overflow:hidden;display:flex;flex-direction:column;align-items:center}.RB{width:400px;height:400px;background-size:cover;background-image:url(/magic-pants/static/media/RB.a4b4a9a3.png);background-position:center center;background-repeat:no-repeat}.PANTS{width:400px;height:400px;background-size:cover;background-position:center center;background-repeat:no-repeat;background-image:black;animation:path 1s infinite}.PANTS .speedAnime{animation:path 0.5s infinite}.answer-container{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.answer-container .answer-title{font-size:24px}.answer-container .answer{width:100%;display:flex;flex-direction:row;justify-content:space-around;align-items:center}.answer-container .answer-item{cursor:pointer;height:100px;width:100px;background-size:cover;background-position:center center;background-repeat:no-repeat}@keyframes path{0%{opacity:1}100%{opacity:0.5}}

.container{position:fixed;color:white;margin:auto;width:100%;height:100%;background-size:cover;perspective-origin:50% 50%;font-family:"DotGothic16", sans-serif}.titleContainer{display:flex;height:100%;width:60%;margin:auto;align-items:center;justify-content:center;flex-direction:column}.title{font-size:80px;margin-bottom:20px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.btnCon{display:flex;justify-content:center;align-items:center;flex-direction:column;font-family:"DotGothic16", sans-serif}.Btn{-webkit-user-select:none;-ms-user-select:none;user-select:none;z-index:10;transition:all 0.5s ease-in-out;background-color:black;border:1px solid white;color:white;padding:10px 30px;text-align:center;text-decoration:none;display:block;font-size:14px;margin:8px 0;border-radius:5px;cursor:pointer}.Btn:hover{background-color:white;border:1px solid black;color:black}

.end-container{position:fixed;color:white;margin:auto;width:100%;height:100%;background-size:cover;perspective-origin:50% 50%;font-family:"DotGothic16", sans-serif}.end-container .titleContainer{display:flex;height:100%;width:60%;margin:auto;align-items:center;justify-content:center;flex-direction:column}.end-container .title{font-size:60px;margin-bottom:20px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.end-container .pantTitle{display:block;margin:0 40px}.end-container .lastSpan{float:right}.text1{animation:textAni 1s 1 forwards;opacity:0}.text2{animation:textAni 1s 1s 1 forwards;opacity:0}.text3{animation:textAni 1s 2s 1 forwards;opacity:0}.text4{animation:textAni 1s 3s 1 forwards;opacity:0}.text5{animation:textAni 1s 4s 1 forwards;opacity:0}@keyframes textAni{0%{opacity:0}100%{opacity:1}}

.doc-container{position:fixed;color:white;margin:auto;width:100%;height:100%;background-size:cover;perspective-origin:50% 50%;font-family:"DotGothic16", sans-serif;display:flex;justify-content:center;align-items:center}.doc-container .wrapper{position:relative;border:1px white solid;width:600px;height:600px;border-radius:5px;margin:auto;padding:16px}.doc-container a{color:white;margin-right:20px}.doc-container .Btn{position:absolute;right:24px;bottom:24px}

.App{width:100vw;height:100vh;background-color:black;display:flex;align-items:center;justify-content:center;letter-spacing:1px;font-family:"DotGothic16", sans-serif}

